<template>
  <div style="background-color: #f8f9fa; height: 100vh">
    <el-container>

      <el-header>
        <projects-manager-header></projects-manager-header>
      </el-header>
      <el-main>
        <div class="App" style=" display: flex; align-items: center;">



          <div style="display: flex; flex-direction: row; flex-flow: wrap; align-items: center;  justify-content: center">

            <el-card @click.native="showAppProjectDlg()">
              <div style="display: flex; align-items: center; flex-direction: column; justify-content: center; height: 150px">
                <i style="color: dodgerblue; font-size: 28px" class="el-icon-plus"/>
                <span style="color: dodgerblue; margin-top: 12px">Add Project</span>
              </div>
            </el-card>

            <el-card v-for="item in projects" @click.native="handleClickProject(item)">
              <div style="display: flex; flex-direction: column; align-content: space-between;">
                  <div style="display: flex; align-content: space-between; align-items: center">
                    <span style="margin-right: auto">{{item.projectName}}</span>

                    <el-dropdown @click.native.stop="" style="align-self: flex-end;">
                      <span class="el-dropdown-link"><i class="el-icon-more" style="padding: 16px"/></span>
                      <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item @click.native.stop="handleEditProject(item)">Edit</el-dropdown-item>
                        <el-dropdown-item  @click.native.stop="deleteProduct = scope.row">Delete</el-dropdown-item>
                      </el-dropdown-menu>
                    </el-dropdown>
                  </div>

                  <span class="text-sec">{{item.projectId}}</span>

                  <span class="text-sec" style="align-self: flex-end"> +{{item.languages == undefined ? 0 : item.languages.split(",").length}} Languages</span>
              </div>

            </el-card>

          </div>


          <el-dialog
              :title="dialogIsEdit? 'Edit Project': 'Add Project'"
              :visible.sync="dialogVisible"
              width="23rem"
              :before-close="handleAddProjectClose">

            <el-form ref="projectForm" :model="projectForm" :rules="rules">
              <el-form-item label="Project Name" prop="projectName">
                <el-input v-model="projectForm.projectName"></el-input>
              </el-form-item>
              <el-form-item label="Android Package Name" prop="packageName">
                <el-input v-model="projectForm.packageName"></el-input>
              </el-form-item>
              <el-form-item label="iOS Bundle Id" prop="bundleId">
                <el-input v-model="projectForm.bundleId"></el-input>
              </el-form-item>
              <el-form-item label="Languages" prop="languages">
                <el-select
                    v-model="projectForm.languagesArray" multiple filterable collapse-tags
                    style="margin-left: 20px;" placeholder="Selected Languages">
                  <el-option v-for="item in languageData" :key="item.value" :label="item.label" :value="item.value" :disabled="item.value == 'EN'">
                  </el-option>
                </el-select>

              </el-form-item>
            </el-form>

            <span slot="footer" class="dialog-footer">
              <el-button @click="dialogVisible = false">Cancel</el-button>
              <el-button type="primary" @click="handleAddProject">{{dialogIsEdit ? "Update": "Add"}}</el-button>
            </span>
          </el-dialog>

          <el-dialog
              title="Hint"
              :visible.sync="isDeleteProjectEmpty"
              width="23rem">
            <span>Are you sure delete {{deleteProduct.projectName}}? </span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="deleteProduct = {}">Cancel</el-button>
                <el-button type="primary" @click="handleDeleteProject">Confirm</el-button>
            </span>
          </el-dialog>

        </div>
        <router-view></router-view>
      </el-main>
    </el-container>

  </div>
</template>

<script>
import ProjectsManagerHeader from "@/components/ProjectsManagerHeader";
import {getProjects, addProject, deleteProject, updateProject} from '../api/index'
import languages from "@/store/languages";

export default {
  data() {
    return {
      languageData: [],
      languageValue: [],
      dialogVisible: false,
      dialogIsEdit: false,
      deleteProduct: {},
      projectForm: {
        projectId: '',
        projectName: '',
        packageName: "",
        bundleId: '',
        languages: '',
        languagesArray: ["EN"]
      },
      rules: {
        projectName: [{required: true}],
      },
      projects: [],
      languageFilterMethod(query, item) {
        return item.label.indexOf(query) > -1;
      }
    };
  },
  components: {
    ProjectsManagerHeader
  },
  computed: {
    isDeleteProjectEmpty() {
      return this.deleteProduct.projectName != null
    }
  },
  methods: {
    handleAddProjectClose() {
      this.$refs.projectForm.resetFields()
    },
    showAppProjectDlg() {
      this.languageValue = ["EN"]
      this.dialogIsEdit = false
      this.dialogVisible = true
    },
    handleAddProject() {
      this.$refs.projectForm.validate((valid) => {
        let languages = ""
        this.projectForm.languagesArray.forEach((item) => {
          languages += ", " + item
        })
        this.projectForm.languages = languages.slice(1)
        console.log(valid, this.projectForm)
        if (valid) {
          this.dialogVisible = false
          if (this.dialogIsEdit) {
            updateProject(this.projectForm).then((date) => {
              if (data.code == 200) {
                // this.project.update(data.data)
              }
            })
          } else {
            addProject(this.projectForm).then((data) => {
              if (data.code == 200) {
                this.project.add(data.data)
              }
            })
          }
        }
      })
    },
    handleModifyLanguageProject(row) {

      this.languageValue = []
      if (row.languages !== undefined) {
        row.languages.split(",").forEach((item) => {
          this.languageValue.push(item.trim())
        })
      }
      console.log(this.languageValue)
      this.modifyLanguagesVisible = true
    },

    handleEditProject(row) {
      this.projectForm.projectId = row.projectId
      this.projectForm.languages = row.languages

      // 构造当前项目的value
      this.projectForm.languagesArray = []
      if (row.languages != undefined) {
        row.languages.split(",").forEach((item) => {
          this.projectForm.languagesArray.push(item.trim())
        })
      }

      this.projectForm.projectName = row.projectName;
      this.projectForm.packageName = row.packageName
      this.projectForm.bundleId = row.bundleId
      this.dialogIsEdit = true;
      this.dialogVisible = true;
    },
    handleDeleteProject() {
      console.log(this.deleteProduct)
      const request = {"projectId": this.deleteProduct.projectId};
      deleteProject(request).then((data) => {
        this.projects =  this.projects.remove(this.deleteProduct)
        this.deleteProduct = {}
      })
    },
    handleClickProject(row) {
      console.log('/project/${row.projectId}/strings')
      this.$store.commit('updateProjectData', row)
      this.$router.push('/project/' + row.projectId + '/dashboard');
    }
  },
  mounted() {
    const data = [];
    Object.keys(languages.state.allLanguages).forEach((item) => {
      data.push({
        label: languages.state.allLanguages[item],
        value: languages.state.languageKey[item],
      })
    })

    this.languageData = data;

    const pageRequest = {"pageSize": 20, "pageNum": 0}
    getProjects(pageRequest).then((data) => {
      this.projects = data.data
      console.log(data)
    })
  }
}
</script>

<style lang="less" scoped>

.text-sec {
  font-size: 14px;
  color: #999999;
}

